import React, {memo, useEffect, useState} from "react";
import "../css/win.css"


import { Display2, Button, ActionButton, IconMic } from '@sberdevices/plasma-ui';
import { IconDownload } from '@sberdevices/plasma-icons';
import {IconRefresh} from "@sberdevices/plasma-icons";

import coin from '../imgs/coin.png'

import ProgressBar from './progressbar'




const Win = (props) => {



    console.log("Win")
    const device = props.device
    const ios = props.ios
    const insets = props.insets
    const character = props.character
    const room = props.room
    const width = props.width
    const height = props.height


    const callbackLikeDislike = props.callbackLikeDislike

    const [propress, setPropress] = useState(0)
    const timer = () => setPropress(propress + 1);


    useEffect(
        () => {
            if (propress > 100) {
                setPropress(100)
                 callbackLikeDislike(false)
            }else{
                const id = setInterval(timer, 50);
                return () => clearInterval(id);
            }

        },
        [propress]
    );



    const like = (like = false) =>{

        console.log("const like = (like = false)")
        callbackLikeDislike(like)

    }


    return (
        <div className="bgwin">


            <div className="modalwin" >

                <div className="rowwin" > ? </div>

                <ProgressBar completed={propress} />


                <div className="rowwin" style={{paddingBottom:"20px", paddingRight:"20px"}}>
                    <Button
                        size="m" view="primary" style ={{ width: "48%", }}
                        onClick={()=> like(true)}
                    >
                        <img src={coin} style={{height:"95%"}}/>
                        <span style={{paddingLeft:"5px"}}></span>
                    </Button>
                    <Button  size="m" view="primary"  style ={{ width: "48%", backgroundColor: "gray"}}
                             onClick={()=> like(false)}
                    > </Button>
                </div>


            </div>
        </div>


    );
};
export default memo(Win);