const Drawing_new = (props) => {

    let rusDict = require('../json/dict.json');
    const device = props.device
    const ios = props.ios
    const insets = props.insets.bottom
    const character = props.character
    const room = props.room
    const user = props.user
    const IamDrawing = props.IamDrawing
    const width = props.width;
    const height = props.height;
    const blr = props.blr
    const [snapshotPoints, loading, error] = useObject(firebase.database().ref('rooms').child(room).child("points"));
    const callbeckSave = props.callbackSaveData


    const [progressFb] = useObjectVal(firebase.database().ref("rooms").child(room).child("word").child("progress"));
    const [isPen, setPen] = useState(true)
    const [brushRadius, setBrushRadius] = useState(3)
    const svgRef = useRef("");

    const canvasDrawRef = useRef();
    const canvasDrawRefRead = useRef();
    let widthCanvas, heightCanvas, leftCanvas, rightCanvas;




    const LoadData1 = () => {


        if (!loading) {

            const lines = JSON.parse(snapshotPoints.val())

            return lines;

            // {"lines":[],"width":479.7,"height":479.7}

        }
        return ""
    }

    const SaveData = () => {
        if (IamDrawing){
            const data = canvasDrawRef.current.getSaveData()

            if (svgRef.current  !== data){

                callbeckSave(data)
                svgRef.current = data
                // classifySVG()

            }
        }
    }

    const handelerPen = () => {

        if (!isPen){
            setBrushRadius(3);
        }else{
            setBrushRadius(20);
        }
        setPen(!isPen)
    }

    rightCanvas = 30
    if (width <=576 ){
        leftCanvas  = 20 +20

    }
    if (width >576 && width <786){
        leftCanvas  = 64 + 20 +20

    }
    if (width >=786){

        leftCanvas = (width  - 40)*0.33 + 40 + 10
        if (leftCanvas>320){
            leftCanvas = 320 +20 + 20
        }
    }


    const h  = height - insets - 135


    widthCanvas = (width  - 20 - 20) - leftCanvas +5
    if (widthCanvas > h){
        widthCanvas = h - h*0.10
    }



    // heightCanvas = widthCanvas


    // if (height>width){
    //     widthCanvas = width * 0.90
    //     heightCanvas = width * 0.90
    // }else{
    //     widthCanvas = 200
    //     heightCanvas = 200
    // //    height * 0.60
    // }


    let widthtools = width * 0.12
    widthtools = widthtools>widthCanvas/7?widthCanvas/7:widthtools
    widthtools = widthtools<20?20:widthtools

    // const data = LoadData1()
    //
    // let prog = props.progress
    // if (!IamDrawing) {
    //         if (progressFb)  prog = progressFb
    //     else
    //         prog = 0;
    // }

    if (progressFb<1){
        if(canvasDrawRefRead.current){
            canvasDrawRefRead.current.clear()
            return ""
        }
    }

    return (
        <div  className = "boxCanvas" style ={{top:  0 + "px", width:"100%", filter: blr }}>

            {IamDrawing &&  <CanvasDraw
                        className = "canvasDraw"
                        ref={canvasDrawRef}
                        canvasWidth = {widthCanvas}
                        canvasHeight = {widthCanvas}
                        brushColor={"#444"}
                        lazyRadius ={0}
                        brushRadius ={brushRadius}
                        erase = {!isPen}
                        onChange = {SaveData}
                        style={{
                            background: null,
                            left: leftCanvas + "px",
                            //right:rightCanvas+ "px",
                            top: 60+30+"px",
                            zIndex:1,
                            boxShadow:
                                "0 13px 27px -5px rgba(50, 50, 93, 0.25),    0 8px 16px -8px rgba(0, 0, 0, 0.3)",
                        }}
                    />}
            {!IamDrawing && !loading && <CanvasDraw
                        className = "canvasDraw"
                        ref={canvasDrawRefRead}
                        hideGrid
                        disabled
                        hideInterface = {true}
                        canvasWidth = {widthCanvas}
                        canvasHeight = {widthCanvas}
                        lazyRadius = {0}
                        brushColor = {"#fff"}
                        saveData = {LoadData1()}
                        style={{
                            background: null,
                            left: leftCanvas + "px",
                            //right:rightCanvas+ "px",
                            top: 60+30+"px",
                            zIndex:1,
                            boxShadow:
                "0 13px 27px -5px rgba(50, 50, 93, 0.25),    0 8px 16px -8px rgba(0, 0, 0, 0.3)",
            }}
                />}
            <div style={{
                left: leftCanvas + "px",
                top: 60+30+"px",
                zIndex:2,
                width:widthCanvas,
                position:"absolute"

            }}>
                {IamDrawing && <ProgressBar completed={props.progress}/>}
                {!IamDrawing && <ProgressBar completed={progressFb}/>}
            </div>





                    {/*style={{ top: height * 0.13 + heightCanvas + 20 + "px", left: (width-widthCanvas) / 2  + "px", width : widthCanvas + "px"}}*/}
                    {IamDrawing && <div className = "toolsPanel" style={{zIndex:1, top:60+30+10+widthCanvas+ "px",  left: leftCanvas+ "px", width: widthCanvas+"px", height: widthCanvas/8 + "px"}}>

                        {isPen  && <button className = "buttonDrawSelected" onClick={handelerPen} style={{backgroundImage: `url(${pen_img})`}}    />}
                        {!isPen && <button className = "buttonDraw"         onClick={handelerPen} style={{backgroundImage: `url(${pen_img})`}}    />}

                        {!isPen && <button className = "buttonDrawSelected" onClick={handelerPen} style={{backgroundImage: `url(${eraser_img})`}} />}
                        {isPen  && <button className = "buttonDraw"         onClick={handelerPen} style={{backgroundImage: `url(${eraser_img})`}} />}

                        <button className = "buttonDraw"     onClick={() => {canvasDrawRef.current.undo();  SaveData()}} style={{backgroundImage: `url(${undo_img})`}} />
                        <button className = "buttonEndDraw"  onClick={() => {canvasDrawRef.current.clear(); SaveData()}} style={{backgroundImage: `url(${trash_img})`}} />
                    </div>}
                </div>
    );
};


export default memo(Drawing_new);


