
import styles from "../../css/chat.css";
import {
    ConversationList,
    Conversation,
    Loader,
    Avatar,

} from "@chatscope/chat-ui-kit-react";
import {useList, useObject} from "react-firebase-hooks/database";
import "firebase/database"
import firebase from "firebase/app";

import React, {memo} from "react";
import zoeIco from "../../imgs/zoeIco.svg";
import {getInitials, generateAvatar, makeid} from "../../names";




const Chat_conversation = (props) => {
    const room = props.room

    console.log("Chat_conversation")
    const [snapshots, loading, error] = useList(firebase.database().ref('rooms').child(room).child("users"));


    return (
        <>
        {loading && <Loader />}
        {!loading && snapshots && (

                <ConversationList >
                    {snapshots.map((v) => (
                        <Conversation name={v.val()["name"]} info={room} key={makeid(5)}>
                            <Avatar  src={generateAvatar(getInitials(v.val()["name"]), "white", "#009578")}
                                     name={v.val()["name"]}

                            />
                        </Conversation>
                        // <React.Fragment key={v.key}>{v.val()}, </React.Fragment>
                    ))}
                    </ConversationList>

        )}
        </>

        // <Conversation name="Lilly" lastSenderName="Lilly" info="Yes i can do it for you">
        //     <Avatar src={zoeIco} name="Lilly" status="available" />
        // </Conversation>
    );
};
export default memo(Chat_conversation);





