export function capitalizeFirstLetter(string) {
    if (string)
        return string.charAt(0).toUpperCase() + string.slice(1);
    return ""
}


export function random_name_bot() {

    let name = ""
    const n = Math.floor(Math.random() * 3)

    switch (n) {
        case 0:{
            name = " "
            break;
        }
        case 1:{
            name = " "
            break;
        }
        default:
            name = " "
    }


    const p = PPP[Math.floor(Math.random() * PPP.length)];


    return p.charAt(0).toUpperCase() + p.slice(1) + name;

};

export function random_name() {

    const p = PPP[Math.floor(Math.random() * PPP.length)];
    const s = SSS[Math.floor(Math.random() * SSS.length)];

    return p.charAt(0).toUpperCase() + p.slice(1) + " " + s

};
