    const nextlider = (room:string) => {

        let u: any  = {} ;
        u = user;

        let userId = u["userId"]


        const roomsRef = firebase.database().ref("rooms");
        let maxOrder = -1
        let maxUser =""

        let minOrder = -1
        let minUser =""

        let count = 0

        roomsRef.child(room).child("users").once('value', (snapshot) => {
                snapshot.forEach((childSnapshot) => {
                    const u = childSnapshot.val()
                    if (u){
                        count += 1;

                        if (u["ninQueue"] >=0 )
                        {
                            const ninQ = u["ninQueue"]
                            if ( (ninQ>=maxOrder)){
                                maxOrder = ninQ;
                                if (childSnapshot.key){
                                    maxUser = childSnapshot.key;
                                }

                            }
                            if ((minOrder === -1) || (ninQ<minOrder)){
                                minOrder = ninQ;
                                if (childSnapshot.key){
                                    minUser = childSnapshot.key;
                                }

                            }
                        }
                    }
                })
                if ((maxUser === userId) && (count ===1)){
                    //  
                    //    
                    return
                }
                if (maxUser !== userId)
                {
                    //  ,   
                    const ref = firebase.database().ref("rooms").child(room).child("users").child(userId);

                    setStatusQuery({
                        status: "I'm thinking.",
                        ninQueue: maxOrder+1

                    })
                    ref.update({
                        status: "I'm thinking.",
                        ninQueue: maxOrder+1,
                        name: u.name
                    } );

                    const ref2 = firebase.database().ref("users-Online/"+ userId );

                    ref2.update({
                        room: room,
                        name: u.name,
                        coin: u.coin,
                        ninQueue: maxOrder+1,
                        status: "I'm thinking."


                    });
                    firebase.database().ref("rooms").child(room).child("points").remove()
                    firebase.database().ref("rooms").child(room).child("word").remove()
                    firebase.database().ref("messages").child(room).remove();
                    firebase.database().ref("liders").child(room).remove();
                }



            })

    }

