import React, {memo, useEffect, useRef, useState} from "react";

import Drawing from "./Drawing_";
import background_img from "../imgs/img.png";
import "../css/game.css"
import firebase from "firebase";
import {useList, useObject, useObjectVal} from "react-firebase-hooks/database";
import axios from "axios";
import {isEmpty} from "../names";
import Chat_main from "./Chat/Chat_main";
import Choice from "./Choice";
import Win from "./Win";



let rusDict = require('../json/dict.json');

const Game = (props) => {
    console.log("Game")
    const device = props.device
    const ios = props.ios
    const insets = props.insets
    const character = props.character
    const room = props.room
    const width = props.width
    const height = props.height
    const user = props.user
    const statusQuery = props.statusQuery
    // const messgs = props.messgs


    const minimumResult = 10

    // const [urlServer, setUrlServer] = useState('')

    const [snapshotServer] = useObject(firebase.database().ref("urlServer"));
    const [ChoiceVal] = useObjectVal(firebase.database().ref("rooms").child(room).child("word"));

    console.log("ChoiceVal", JSON.stringify(ChoiceVal))

    const arrayResult = useRef([])
    const choiseRef = useRef({})

    choiseRef.current = ChoiceVal
    const refServerUrl = useRef("")
    const [progress, setProgress] = useState(0)

    const [winShow, setWinShow] = useState(true)



    const timer = () => setProgress((progress)=>{
        if (!isEmpty(ChoiceVal)){
            firebase.database().ref("rooms").child(room).child("word").update({progress:Math.floor(progress)})
            return progress + 0.1
        }
        return progress;
    });



    useEffect(() => {
            if ((statusQuery.status === "I'm drawing.")){
                if (progress > 100) {
                    setProgress(0)
                    props.callbackCancelChoice()
                }
                const id = setInterval(timer, 6000);
                return () => clearInterval(id);
            }
        }, [progress, ChoiceVal]);


    useEffect(() => {
        console.log("useEffect")
        if (refServerUrl.current==="")
            if (snapshotServer){
            const val = snapshotServer.val()
            console.log(snapshotServer.val())
            refServerUrl.current = val
            // setUrlServer(val)
        }

    }, []);

    if (refServerUrl.current===""){
        if (snapshotServer){

            const val = snapshotServer.val()
            console.log(val)
            refServerUrl.current = val
        }

    }

    const callbackLikeDislike = React.useCallback((like) => {

        setWinShow(false)
        console.log(like)
        // const refirebase = firebase.database().ref("rooms")
        // refirebase.child(room).update({
        //     points: JSON.stringify(data)
        // });
        // classifySVG()
        //
        // firebase.database().ref("rooms").child(room).child("points").onDisconnect().remove();
    }, [])

    const callbackSaveData = React.useCallback((data) => {

        const refirebase = firebase.database().ref("rooms")
        refirebase.child(room).update({
            points: JSON.stringify(data)
        });
        classifySVG()

        firebase.database().ref("rooms").child(room).child("points").onDisconnect().remove();
    }, [])

    const callbackChoice = React.useCallback((word) => {


        // setChoice(word)

        const ref = firebase.database().ref("rooms")
        ref.child(room).update({
            word: word
        });
        ref.child(room).onDisconnect().remove();

        setProgress(0)


    }, [])

    let blr = "blur(0)"

    if (statusQuery.status === "I'm drawing." && isEmpty(ChoiceVal)) {
        blr = "blur(8px)"
    }

    const win = (user) => {
        setWinShow(true)
        props.win(user)
    }

    const addResult = (result) =>{
        console.log(result, JSON.stringify(choiseRef.current))
        if (result) {
            let res = result.result

            res = Math.round(res * 100) / 100;
            if (res > minimumResult) {
                const roomsRef = firebase.database().ref("rooms").child(room).child("users")
                roomsRef.once('value', (snapshot) => {
                    const bots = []
                    snapshot.forEach((childSnapshot) => {
                        const user = childSnapshot.val()
                        if (user){
                            if (user.hasOwnProperty("bot")){
                                bots.push(user)
                            }
                        }
                    })
                    if (bots.length>0){
                        const u = bots[Math.floor(Math.random() * bots.length)]

                        let name = result['name']
                        if ((!arrayResult.current.includes(name)) ) {

                            arrayResult.current.push(name );

                            props.callbackSendMessage(name + "(" + res.toString() + ")", u)
                            if (!isEmpty(choiseRef.current)){
                                if (choiseRef.current.hasOwnProperty("rus")){
                                    if ( name.toLowerCase() === choiseRef.current.rus.toLowerCase())
                                    {
                                        win(user)
                                    }
                                }
                            }
                        }
                    }


                })
                // let name = result['name']
                //
                //
                // if ((!arrayResult.includes(name)) || (arrayResult[arrayResult.length - 1] !== name)) {
                //     setArrayResult([...arrayResult, name]);
                //
                //     const obj = {id: arr.length + 1, name: random_name(), message: name + " (" + res + ")"}
                //     setArr([...arr, obj]);
                //
                // }


            }
        }

    }

    const classifySVG =() =>{


        let headers = new Headers();

        headers.append('Content-Type', 'application/json');
        headers.append('Accept', 'application/json');

        axios.get(refServerUrl.current + room)
            .then(res => {
                const classyfyArray = res.data;

                // const valueClass = classyfyArray[0].result
                // const nameClass = classyfyArray[0].name

                for (const [key, value] of Object.entries(classyfyArray)) {
                    // console.log(key, value);
                    if   (rusDict[classyfyArray[key].name]){
                        // console.log(classyfyArray[key].name)

                        const newjson = {name:rusDict[classyfyArray[key].name], result:classyfyArray[key].result}
                        classyfyArray[key] = newjson;
                        // classyfyArray[key].name = rusDict[classyfyArray[key].name]
                    }

                }
                // setSlassyfy(classyfyArray["0"])
                addResult(classyfyArray["0"])
            })
    }

    // console.log("isEmpty(ChoiceVal)", isEmpty(ChoiceVal), ChoiceVal)
    //
    // console.log('statusQuery.status === "I\'m drawing."', statusQuery.status === "I'm drawing.")


    let showChoice = false;
    if (statusQuery.status === "I'm drawing."){
        if (isEmpty(ChoiceVal)){
            showChoice = true;
        }else{
            if (!ChoiceVal.hasOwnProperty("rus")) {
                showChoice = true;
            }
        }

    }


    return (
        <>
            <img className = "bg" src={background_img} alt="" />

            <Chat_main room = {room}
                       device = {device}
                       insets={insets}
                       user = {user}
                       ios = {ios}
                       character={character}
                       width = {width}
                       height = {height}
                       room = {room}
                       // messages = {props.snapshotsmessages}
                       callbackSendMessage = {props.callbackSendMessage}
                       IamDrawing = {statusQuery.status === "I'm drawing."}
                       choice = {ChoiceVal}
                       blr = {blr}

            />
            {<Drawing room = {room}
                      device = {device}
                      insets={insets}
                      user = {user}
                      ios = {ios}
                      character={character}
                      width = {width}
                      height = {height}
                      callbackSaveData = {callbackSaveData}
                      IamDrawing = {statusQuery.status === "I'm drawing."}
                      progress = {progress}
                      blr = {blr}
            />
            }
            {!winShow && showChoice && <Choice device = {device}
                                   insets={insets}
                                   ios = {ios}
                                   character={character}
                                   user = {user}
                                   room = {room}
                                   width = {width}
                                   height = {height}
                                   callbackChoice = {callbackChoice}
                                   callbackCancelChoice = {props.callbackCancelChoice}

            />}

            {winShow && <Win device = {device}
                                   insets={insets}
                                   ios = {ios}
                                   character={character}
                                   user = {user}
                                   room = {room}
                                   width = {width}
                                   height = {height}
                                   callbackLikeDislike = {callbackLikeDislike}


            />}


         </>
    );
};
export default memo(Game);






